input[type="radio"] {
  display: none;
}

.radio-box {
  font-size: 28px;
  color: whitesmoke;
  padding: 10px 25px;
  border-radius: 1px;
  cursor: pointer;
  transition: 0.2s;
}

.box-checked-false:hover {
  color: #464646;
  background-color: #ffff1c;
}

.box-checked-true {
  background-color: #ffff1c;
  color: #464646;
}
