.main {
  padding: 300px 0 100px 0;
  background-image: linear-gradient(to bottom, #9cecfb, #65c7f7, #0052d4);
}
.container {
  width: 92%;
  margin: 0 auto;
  margin-bottom: 80px;
}

.radio-boxes {
  display: flex;
  flex-wrap: wrap;
}

.quote-area {
  border: 3px solid #ffff1c;
  padding: 80px 25px;
}
.quote-window {
  color: whitesmoke;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 0 10px;
  margin-bottom: 40px;
}

.quote-window__quote {
  margin: 30px 0;
  font-size: 22px;
}
.quote-window__person {
  align-self: flex-end;
  margin: 0 0 10px 0;
  font-size: 18px;
}

.quote-scroller {
  display: flex;
  justify-content: space-around;
}

.quote-scroller img {
  cursor: pointer;
}

@media (max-width: 600px) {
  .radio-boxes {
    justify-content: space-around;
  }
}
