.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url(./bg.jpg) no-repeat center;
  background-size: cover;
  height: 100vh;
  min-height: 600px;
}

.header__title {
  color: whitesmoke;
  margin-bottom: 150px;
  text-align: center;
  font-size: 27px;
}

.header__title-span {
  text-transform: uppercase;
  font-size: 72px;
  background: -webkit-linear-gradient(#edf57f, #ffff1c);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header-anchor__btn {
  border: none;
  cursor: pointer;
  color: whitesmoke;
  background-color: transparent;
  border: 2px solid #ffff1c;
  border-radius: 8px;
  padding: 25px 60px;
  font-size: 30px;
  font-weight: 700;
  transition: 0.5s;
}

.header-anchor__btn:hover {
  color: #464646;
  background-color: #ffff1c;
  font-size: 2em;
}

@media (max-width: 600px) {
  .header-anchor__btn {
    color: #464646;
    background-color: #ffff1c;
  }
}
